import {
  Box,
  Button,
  Card,
  Divider,
  Grid,
  styled,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { useTranslation } from "react-i18next";

import { ButtonText, H3, H6, Small } from "components/Typography";

import { useNavigate } from "react-router-dom";
import AppTextField from "components/input-fields/AppTextField";
import { useState } from "react";

// let padding;
// if (window.innerWidth > 1720 && !isLaptop) {
//   padding = "50px 165px";
// } else {
//   padding = isLaptop ? "30px 15px 50px 15px" : "50px 80px";
// }

const StyledCard = styled(Card)(({ theme }) => ({
  width: "100%",
  // padding: padding,
  padding: "50px 15px",
  flex: 1,
  backgroundColor: "#FFFFFF",
  borderRadius: 0,
  border: `1px solid ${theme.palette.divider}`,
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",

  // justifyContent: "center",
  [theme.breakpoints.down("sm")]: {
    "& > .MuiBox-root": {
      paddingBottom: 0,
    },
  },
}));

export default function About({}) {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));
  const isTablet = useMediaQuery(theme.breakpoints.down("md"));
  const isLaptop = useMediaQuery(theme.breakpoints.down("lg"));
  const navigate = useNavigate();
  const { t } = useTranslation();

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");

  return (
    <StyledCard>
      <Box
        sx={{
          width: isLaptop ? "100%" : "800px",
        }}
      >
        <Box mb={8} sx={{ display: "flex", justifyContent: "center" }}>
          <img
            src={"/static/logos/forsea-big.png"}
            alt="logo"
            style={{
              width: "210px",
            }}
          />
        </Box>

        {/* 포어씨 회사소게 */}
        <Grid container gap={8}>
          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("forsea.company.introduction.title")}</H3>
            <Small sx={{ whiteSpace: "pre-line" }}>
              {t("forsea.company.introduction.description")}
            </Small>
          </Grid>

          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("forsea.company.business.title")}</H3>
            <Small sx={{ whiteSpace: "pre-line" }}>
              {t("forsea.company.business.description")}
            </Small>
          </Grid>
        </Grid>

        <Divider sx={{ width: "100%", height: "1px", margin: "50px 0" }} />

        {/* FUKUNARI 회사소개 */}
        <Box mb={8} sx={{ display: "flex", justifyContent: "center" }}>
          <img
            src={"/static/logos/fukunari-big.png"}
            alt="logo"
            style={{
              width: "180px",
            }}
          />
        </Box>

        <Grid container gap={8}>
          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("fukunari.company.introduction.title")}</H3>
            <Small sx={{ whiteSpace: "pre-line" }}>
              {t("fukunari.company.introduction.description")}
            </Small>
          </Grid>

          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("fukunari.company.business.title")}</H3>
            <Small sx={{ whiteSpace: "pre-line" }}>
              {t("fukunari.company.business.description")}
            </Small>
          </Grid>
        </Grid>

        <Divider sx={{ width: "100%", height: "1px", margin: "50px 0" }} />

        {/* 사용 안내 */}
        {/* <Grid container gap={8}>
          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("how_to_use_title")}</H3>
            <Small sx={{ whiteSpace: "pre-line" }}>
              {t("how_to_use_description")}
            </Small>
          </Grid>
        </Grid>

        <Divider sx={{ width: "100%", height: "1px", margin: "50px 0" }} /> */}

        {/* 주의사항 */}
        {/* <Grid container gap={8}>
          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("caution_title")}</H3>
            <Small sx={{ whiteSpace: "pre-line" }}>
              {t("caution_description")}
            </Small>
          </Grid>
        </Grid>

        <Divider sx={{ width: "100%", height: "1px", margin: "50px 0" }} /> */}

        {/* 연락처 */}
        <Grid container gap={8}>
          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("contact")}</H3>
            <Grid container spacing={4}>
              <ContactInfo
                logo="/static/logos/forsea-small.png"
                phone="051-242-5300"
                fax="051-242-5321"
                isTablet={isTablet}
                isLast={false}
                t={t}
              />
              <ContactInfo
                logo="/static/logos/fukunari-small.png"
                phone="81-96-322-3112"
                fax="81-96-322-3113"
                isTablet={isTablet}
                isLast={true}
                t={t}
              />
            </Grid>
          </Grid>
        </Grid>

        <Divider sx={{ width: "100%", height: "1px", margin: "50px 0" }} />

        {/* 상담내용 등록하기 */}
        <Grid container gap={8}>
          <Grid container item gap={2} sx={{ justifyContent: "flex-start" }}>
            <H3>{t("common.register_consultation")}</H3>
            {/* <Small sx={{ whiteSpace: "pre-line" }}>
              {t("caution_description")}
            </Small> */}

            <Grid item xs={12}>
              <H6 mb={1}>{t("common.name")}</H6>
              <AppTextField
                fullWidth
                type="text"
                name="name"
                placeholder={t("common.name")}
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </Grid>

            <Grid item xs={12}>
              <H6 mb={1}>{t("common.email")}</H6>
              <AppTextField
                fullWidth
                type="email"
                name="email"
                placeholder={t("common.email")}
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </Grid>

            <Grid item xs={12}>
              <Small color={theme.palette.primary.dark}>
                {t("common.consultation_content")}
              </Small>
              <AppTextField
                type="text"
                // name="refundReason"
                placeholder={t("common.consultation_content")}
                // value={refundReason}
                // onChange={(e) => setRefundReason(e.target.value)}
                sx={{
                  mt: 1,
                  "& .MuiInputBase-root": {
                    height: "auto",
                  },
                  "& .MuiInputBase-input": {
                    height: "100px",
                    alignItems: "flex-start",
                  },
                }}
                multiline
                rows={4}
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                // onClick={() => navigate("/forum/write")}
                variant="contained"
                size="small"
                sx={{
                  // height: "34px",
                  fontSize: 14.5,
                  borderRadius: 2,
                  width: "100%",
                  whiteSpace: "nowrap",
                  borderRadius: 2,
                  background: theme.palette.primary.darkBlue,
                  "&:hover": {
                    background: theme.palette.primary.darkBlueHover,
                  },
                }}
              >
                <ButtonText>{t("common.register_consultation")}</ButtonText>
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </StyledCard>
  );
}

const ContactInfo = ({ logo, phone, fax, isTablet, isLast, t }) => (
  <Grid item xs={12} sm={6}>
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-start",
        borderRight: isLast || isTablet ? "none" : "1px solid #e1e1e1",
        borderBottom: isTablet ? "1px solid #e1e1e1" : "none",
        paddingY: 2,
        height: "100%",
      }}
    >
      <img src={logo} width={120} alt="logo" style={{ marginBottom: "10px" }} />
      <Box sx={{ display: "flex", flexDirection: "column", gap: 1 }}>
        <ContactDetail label={t("common.phone")} value={phone} />
        {fax && <ContactDetail label={t("common.fax")} value={fax} />}
      </Box>
    </Box>
  </Grid>
);

const ContactDetail = ({ label, value, t }) => (
  <Box sx={{ display: "flex", alignItems: "center" }}>
    <Small sx={{ fontWeight: 600, marginRight: "10px" }}>{label}</Small>
    <Small>{value}</Small>
  </Box>
);
